<template>
  <v-app>
    <v-main>
      <div v-if="!isPhone">
        <SideBar class="theme--dark">
          <router-view/>
          <footer>

          </footer>
        </SideBar>
      </div>
      <div style="width: 100%; height: 100%" v-else>
        <PhonePage style="width: 100%; height: 100%"/>
      </div>
      <v-dialog
          v-model="setup"
          persistent
      >
        <v-card class="text-center">
          <h1>{{ $t("setup.title") }}</h1>
          <v-stepper v-model="e1" style="padding-bottom: 1em;">
            <v-stepper-header>

              <v-stepper-step
                  :complete="e1 > 1"
                  step="1"
              >
                {{ $t("setup.step1.title") }}
              </v-stepper-step>

              <v-divider></v-divider>

              <v-stepper-step
                  :complete="e1 > 2"
                  step="2"
              >
                {{ $t("setup.step2.title") }}
              </v-stepper-step>

              <v-divider></v-divider>

              <v-stepper-step :complete="e1 > 3" step="3">
                {{ $t("setup.step3.title") }}
              </v-stepper-step>
            </v-stepper-header>

            <v-stepper-items>
              <v-stepper-content step="1">
                <v-card
                    class="mb-12"
                    height="200px"
                    style="padding: 60px;"
                >
                  <v-select
                      @change="changeLang(selectLang)"
                      v-model="selectLang"
                      :items="langList"
                      item-text="name"
                      prepend-icon="mdi-translate"
                      item-value="id"
                      menu-props="auto"
                      label="Select"
                      persistent-hint
                      return-object
                      single-line
                  ></v-select>
                </v-card>
                <v-btn
                    v-if="e1 < 2"
                    color="primary"
                    @click="e1 += 1"
                    :disabled="selectLang === null"
                >
                  {{ $t("setup.next") }}
                </v-btn>
                <br>
              </v-stepper-content>

              <v-stepper-content step="2">
                <v-card
                    class="mb-12"
                    height="200px"
                    style="padding: 60px;"
                >
                  <v-select
                      @change="changeServer(selectServer)"
                      v-model="selectServer"
                      :items="servers"
                      prepend-icon="mdi-server"
                      item-text="name"
                      item-value="id"
                      menu-props="auto"
                      label="Select"
                      persistent-hint
                      return-object
                      single-line
                  ></v-select>
                </v-card>
                <v-btn
                    v-if="e1 < 3"
                    color="primary"
                    @click="e1 += 1"
                    :disabled="selectServer === null"
                >
                  {{ $t("setup.next") }}
                </v-btn>
                <br>

              </v-stepper-content>

              <v-stepper-content step="3">
                <v-card
                    class="mb-12"
                    height="200px"
                    style="padding: 60px;"

                >

                  <h3>{{ $t("setup.step3.desc", {server: getServerName}) }}</h3>
                  <br>
                  <v-icon v-if="finishDownload"
                          color="success">mdi-check</v-icon>
                  <v-btn v-else-if="!loading" @click="loadData(selectServer)">
                    <v-icon>mdi-play</v-icon>
                  </v-btn>
                  <v-progress-linear
                      :active="loading"
                      :value="loadingValue"
                      rounded
                      v-else
                      :indeterminate="indexing"
                  ></v-progress-linear>
                  <br>
                  <p v-if="loading">Loading: <code>{{ nowLoading }}</code> </p>


                </v-card>
                <v-btn
                    v-if="e1 < 4"
                    color="success"
                    @click="doneSetup"
                    :disabled="!finishDownload"
                >
                  {{ $t("setup.done") }}
                </v-btn>
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>
        </v-card>
      </v-dialog>
    </v-main>
  </v-app>
</template>

<script>
import SideBar from '@/components/SideBar/SideBar.vue'
import { langList } from "@/i18n.js"
import {pinyin} from "pinyin-pro";
import axios from "axios";
import PhonePage from "@/PhonePage";

export default {
  name: 'App',

  data () {
    return {
      lang: this.$i18n.locale,
      langList: langList,
      e1: 1,
      selectLang: null,
      selectServer: null,
      downloaded: null,
      loading: false,
      finishDownload: false,
      loadingValue: 0,
      nowLoading: "...",
      isPhone: (function(a){if(/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(a)||/1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\\-(n|u)|c55\/|capi|ccwa|cdm\\-|cell|chtm|cldc|cmd\\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\\-s|devi|dica|dmob|do(c|p)o|ds(12|\\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\\-|_)|g1 u|g560|gene|gf\\-5|g\\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\\-(m|p|t)|hei\\-|hi(pt|ta)|hp( i|ip)|hs\\-c|ht(c(\\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\\-(20|go|ma)|i230|iac( |\\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\\-[a-w])|libw|lynx|m1\\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\\-2|po(ck|rt|se)|prox|psio|pt\\-g|qa\\-a|qc(07|12|21|32|60|\\-[2-7]|i\\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\\-|oo|p\\-)|sdk\/|se(c(\\-|0|1)|47|mc|nd|ri)|sgh\\-|shar|sie(\\-|m)|sk\\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\\-|v\\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\\-|tdg\\-|tel(i|m)|tim\\-|t\\-mo|to(pl|sh)|ts(70|m\\-|m3|m5)|tx\\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\\-|your|zeto|zte\\-/i.test(a.substr(0,4))) return true;})(navigator.userAgent||navigator.vendor||window.opera)
    }
  },
  components: {
    SideBar,
    PhonePage
  },
  computed: {
    setup: function () {
      return !this.$store.state.done
    },
    servers: function () {
      let servers = ["en_US", "ja_JP", "ko_KR", "zh_CN", "zh_TW"]
      let Servers = []
      servers.forEach(s => {
        Servers.push({
          id: s,
          name: this.$t(`setup.servers.${s}`)
        })
      })
      return Servers
    },
    getServerName: function () {
      if (this.selectServer!== null){
        return this.selectServer.name
      }
      return null
    },
    indexing: function() {
      return this.loadingValue === 100
    }
  },
  methods: {
    changeLang(l){
      this.$i18n.locale = l.id;
      window.localStorage.setItem("language", l.id)
      window.localStorage.setItem("langName", l.name)
    },
    changeServer(s){
      window.localStorage.setItem("server", JSON.stringify(s))
    },
    doneSetup () {
      this.$store.commit("doneSetup")
      this.$store.commit("reloadData")
    },
    loadData: function (selectServer) {
      this.loadingValue = 0
      this.loading = true
      const urls = {
        "zone_table": `https://raw.githubusercontent.com/Kengxxiao/ArknightsGameData/master/${selectServer.id}/gamedata/excel/zone_table.json`,
        "stage_table": `https://raw.githubusercontent.com/Kengxxiao/ArknightsGameData/master/${selectServer.id}/gamedata/excel/stage_table.json`,
        "chapter_table": `https://raw.githubusercontent.com/Kengxxiao/ArknightsGameData/master/${selectServer.id}/gamedata/excel/chapter_table.json`,
        "character_table": `https://raw.githubusercontent.com/Kengxxiao/ArknightsGameData/master/${selectServer.id}/gamedata/excel/character_table.json`
      }
      let now = 1;
      setTimeout(async ()=> {
        for (const k of Object.keys(urls)) {
          this.nowLoading = k.replace("_", " ")
          await axios.get(urls[k]).then(data => {
            let json = data.data
            if (k === "character_table") {
              Object.keys(json).forEach(c => {
                json[c]["pinyinFull"] = pinyin(json[c].name, {toneType: "none", type: "array"}).join("")
                json[c]["pinyinHead"] = pinyin(json[c].name, {toneType: "none", pattern: "initial", type: "array"}).join("")
              })
            }
            window.localStorage.setItem(k, JSON.stringify(json));
          })
          this.loadingValue = (now / (Object.keys(urls).length+1))*100

          now++
          await new Promise(resolve => setTimeout(resolve, 1000));
        }
        this.loadingValue = 100
        this.nowLoading = "Importing..."
        await new Promise(resolve => setTimeout(resolve, 3000));

        this.loading = false
        this.finishDownload = true

      })
    },
  },
  mounted() {
    this.$root.$on("openLang", ()=> {

    })
  },
  created() {
    this.$root.$refs.App = this;
    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', e => {
       this.$vuetify.theme.dark = e.matches
    });
    if (window.localStorage.getItem("autoDark")===null){
      window.localStorage.setItem("autoDark", "true")
    }
    if (window.localStorage.getItem("autoDark")==="true"){
      this.$vuetify.theme.dark = window.matchMedia("(prefers-color-scheme: dark)").matches
    } else this.$vuetify.theme.dark = window.localStorage.getItem("darkTheme") === "true";

  }
}
</script>

